import { createAction } from "@reduxjs/toolkit";
import { BookRecordsArray, FilterObject } from "types";
interface ShowError {
    isError: boolean;
    errorMessage: string;
}

export const showPreviousDetails = createAction("PREVIOUS_DETAILS_SHOW");
export const showNextDetails = createAction("NEXT_DETAILS_SHOW");
export const toggleSnackBar = createAction<string>("SNACKBAR_TOGGLE");
export const closeSnackBar = createAction("SNACKBAR_CLOSE");
export const filterBooks = createAction<FilterObject>("BOOKS_FILTER");
export const removeBook = createAction<{ id: string }>("BOOK_REMOVE");
export const changePage = createAction<number>("PAGE_CHANGE");
export const cacheSupported = createAction("CACHE_SET_SUPPORTED");
export const fetchDetails = createAction<string>("DETAILS_FETCH");
export const fetchBooksFromFavorites = createAction<{ data: BookRecordsArray }>("BOOKS_FETCH");
export const setIsFromNetwork = createAction<boolean>("IS_FETCHED_FROM_URL_SET");
export const showError = createAction<ShowError>("ERROR_SHOW");
export const sortBook = createAction<number>("BOOK_SORT");
